import JobSeekerState from "./jobSeeker.state";


export default function JobSeekerReducer (state = JobSeekerState, action) {

    switch (action.type) {
        case "CHANGE_FIRST_NAME":
            return{
                ...state,
                editor_menu: {
                    ...state.editor_menu,
                    current_edit: {
                        ...state.editor_menu.current_edit,
                        title: {
                            ...state.editor_menu.current_edit.title,
                            firstName: action.payload
                        }
                    }
                }
                // userName: action.payload
            };
        case "SOME_TYPE2":
            return{
                ...state,
                // dataLoading: action.payload
            };
        default:
            return{
                ...state
            };
    }

}
