    setErrorSnackBar = (message, type) => {
        this.setState({
            ...this.state,
            errorType: type?type:'error',
            errorMessage: message?message:null
        })
    };

    getCvs = () =>{
        this.setRefreshButton();
        this.getProfile().then((response) => {
            this.setState({
                ...this.state,
                cvList: response.data.cvList,
            });
            let cvData = {};
            let chosenCvs = {};
            response.data.cvList.map(item => {
                cvData[item.cvId]=null;
                chosenCvs[item.cvId]=false;
                return false
            });

            this.setState({
                ...this.state,
                getCVerror:false,
                cvData: {
                    ...this.state.cvData,
                    ...cvData
                },
                chosenCvs:{
                    ...this.state.chosenCvs,
                    ...chosenCvs
                }
            });

        }).catch((error) => {
            this.setRefreshButton(true);
            this.setState({
                ...this.state,
                getCVerror:true,
                errorMessage: error,
            });
        });

    };

