    deleteCv = () => {
        this.setState({
            ...this.state,
            chosenBar:{
                ...this.state.chosenBar,
                number: 0
            },
            dialogDelete: false,
            dialogLoading: true});

        let arr = [];
        Object.entries(this.state.chosenCvs).map(item =>{
                return item[1]?arr.push(item[0]):false;
        });

        let promisesArray = arr.map(item => {
            return new Promise((resolve, reject) => {
                this.delete(item).then(request=>{
                    resolve(request);
                }).catch(error=>{
                    reject(error);
                });
            })
        });

        Promise.all(promisesArray)
            .then((results)=> {
                // console.log(results);
                this.selectAll('unselect');


                this.getProfile().then((response) => {
                    this.setState({
                        ...this.state,
                        cvList: response.data.cvList,
                    });
                    let cvData = {};
                    let chosenCvs = {};
                    response.data.cvList.map(item => {
                        cvData[item.cvId]=null;
                        chosenCvs[item.cvId]=false;
                        return false
                    });

                    this.setState({
                        ...this.state,
                        cvData: {
                            ...cvData
                        },
                        chosenCvs:{
                            ...chosenCvs
                        }
                    });
                    this.setState({...this.state,dialogLoading: false});
                    this.setErrorSnackBar('deleted','success')

                }).catch((error) => {
                    this.selectAll('unselect');
                    this.setState({...this.state,dialogLoading: false});
                    this.setState({
                        ...this.state,
                        errorMessage: error,
                    });
                    this.setErrorSnackBar(error.toString())
                });
            })
            .catch((error) => {
                this.selectAll('unselect');
                this.setState({...this.state,dialogLoading: false});
                this.setErrorSnackBar(error.toString())
            });





    };

    activateDeactivate = (activateDeactivate) => {
        this.setState({
            ...this.state,
            chosenBar:{
                ...this.state.chosenBar,
                number: 0
            },
            dialogLoading: true
           });

        let arr = [];
        Object.entries(this.state.chosenCvs).map(item =>{
            return item[1]?arr.push(item[0]):false;
        });

        let promisesArray = arr.map(item => {
            return new Promise((resolve, reject) => {
                if (activateDeactivate === 'activate') {
                    this.activate(item).then(request => {
                        resolve(request);
                    }).catch(error => {
                        reject(error);
                    });
                }else{
                    this.deactivate(item).then(request => {
                        resolve(request);
                    }).catch(error => {
                        reject(error);
                    });
                }
            })
        });

        Promise.all(promisesArray)
            .then((results)=> {
                // console.log(results);
                this.selectAll('unselect');


                this.getProfile().then((response) => {
                    this.setState({
                        ...this.state,
                        cvList: response.data.cvList,
                    });
                    let cvData = {};
                    let chosenCvs = {};
                    response.data.cvList.map(item => {
                        cvData[item.cvId]=null;
                        chosenCvs[item.cvId]=false;
                        return false
                    });

                    this.setState({
                        ...this.state,
                        cvData: {
                            ...cvData
                        },
                        chosenCvs:{
                            ...chosenCvs
                        }
                    });
                    this.setState({...this.state,dialogLoading: false});
                    this.setErrorSnackBar(activateDeactivate==='activate'?'activated'
                        :'deactivated','success')

                }).catch((error) => {
                    this.selectAll('unselect');
                    this.setState({...this.state,dialogLoading: false});
                    this.setState({
                        ...this.state,
                        errorMessage: error,
                    });
                    this.setErrorSnackBar(error.toString())
                });
            })
            .catch((error) => {
                this.selectAll('unselect');
                this.setState({...this.state,dialogLoading: false});
                this.setErrorSnackBar(error.toString())
            });





    };
