import { combineReducers } from "redux";
import CoreReducer from "./core.reducer";
import JobSeekerReducer from "../../../job_seeker/store/jobSeeker.reducer";
import HiringReducer from "../../../hiring/store/hiring.reducer";

const rootReducer = combineReducers({
   CoreReducer,
   JobSeekerReducer,
   HiringReducer
});

export default rootReducer;
