import Close from '@material-ui/icons/Close';
import Save from '@material-ui/icons/Save';
import Refresh from '@material-ui/icons/Autorenew';
import DeleteForever from '@material-ui/icons/DeleteForever';
import CheckBoxOutlineBlankOutlined from '@material-ui/icons/CheckBoxOutlineBlankOutlined';
import CheckBoxOutlined from '@material-ui/icons/CheckBoxOutlined';
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import {
    ActivateAdvertising,
    ErrorSnackBar,
    InputSingleRow,
    InputValidateSingleRow,
    LoadingDialog
} from "../../theme/cvBankForms";



const styles = theme => ({
    menuButtonEditor: {
        position: 'fixed',
        marginTop: 'calc(100vh - 16.5em)',
        marginLeft: 'calc(100% - 8em)',
        zIndex: 1000,
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    menuButtonPalette: {
        position: 'fixed',
        marginTop: 'calc(100vh - 16.5em)',
        marginLeft: 'calc(100% - 7em)',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },

    content: {
        position:'relative',
        flexGrow: 1,
    },
    paper:{
        background: 'white !important',
    },
    expansionPanel: {
        boxShadow: 'none'
    },
    margin:{
        margin:'0 4px 0 4px'
    }

});



class SettingsComponent extends Component{

    state = {
        mobileOpenL: false,
        mobileOpenR: false,
        cvList: null,
        cvData: null,
        snackbarIsOpen: false,
        errorMessage: null,
        errorType: 'error',
        chosenCvs: null,
        chosenBar: {
            number: 0,
            open: false
        },
        dialogDelete: false,
        dialogVisible: false,
        dialogLoading: false,
        refreshButton: false,
        personData: null
    };

    handleDrawerToggleL = () => {
        this.setState(state => ({
            ...this.state,
            mobileOpenL: !state.mobileOpenL }));
    };

    registration = () =>{
        axios({
            method:'post',
            url:'/jobSeeker/registration',
            baseURL: BASE_URL,
            auth: {
                username: 'test2NS',
                password: '123testNS$'
            },
            data:{
                firstName: 'test1NS',
                lastName: 'NS',
                email: 'test1NS@mail.com',
                phone: '8999999'
            },
            timeout: 1000
        }).then(function (response) {
            const tooken = response.config.headers.Authorization.split(' ')[1];
            // console.log(tooken);
            localStorage.setItem('tooken',JSON.stringify(tooken))
            // console.log(response.data);
            // console.log(response.status);
            // console.log(response.statusText);
            // console.log(response.headers);
            // console.log(response.config);
        }).catch(function (error) {
            console.log(error);
        })
    };

    login = () => {
        axios({
            method:'get',
            url:'/jobSeeker/login',
            baseURL: BASE_URL,
            auth: {
                username: 'test2NS',
                password: '123testNS$'
            },
            // headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 5000
        }).then(function (response) {
            const tooken = response.config.headers.Authorization.split(' ')[1];
            localStorage.setItem('tooken',JSON.stringify(tooken));

            // console.log(response.data);
            // console.log(response.status);
            // console.log(response.statusText);
            // console.log(response.headers);
            // console.log(response.config);
        }).catch(function (error) {
            console.log(error);
        });
    };

    getProfile = () => {
        let req = axios({
            method:'get',
            url:'/jobSeeker/profile',
            baseURL: BASE_URL,
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 15000
        });

        return req;
    };

    delete = (cvId) => {
        let req = axios({
            method:'delete',
            url:'/jobSeeker/cv',
            baseURL: BASE_URL,
            params: {
                cvId: cvId
            },
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 15000
        });

        return req;
    };

    getCv = (id) => {
        let req = axios({
            method:'get',
            url:`/jobSeeker/cv?cvId=${id}`,
            baseURL: BASE_URL,
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 15000
        });

        return req;
    };

    addCV = () => {

        this.setState({...this.state,dialogLoading: true});

        const date1 = new Date(2014, 1, 1);
        const date2 = new Date(2015, 1, 1);
        const date3 = new Date(2015, 1, 1);
        // token: 'dGVzdDFOUzoxMjN0ZXN0TlMk'

        const possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

        axios({
            method:'put',
            url:'/jobSeeker/cv',
            baseURL: BASE_URL,
            // auth: {
            //     username: 'test2NS',
            //     password: '123testNS$'
            // },
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            data: {
                cvId: `test${possible.charAt(Math.floor(Math.random() * possible.length))}${Math.floor(Math.random() * Math.floor(9999))}`+
                    `${possible.charAt(Math.floor(Math.random() * possible.length))}`+
                    `${possible.charAt(Math.floor(Math.random() * possible.length))}`+
                    `${possible.charAt(Math.floor(Math.random() * possible.length))}`,
                cvName: "Mytestcv",
                title: {
                    firstname: "test1NS",
                    isFirstnameVisible: true,
                    lastName: "NS",
                    isLastNameVisible: true,
                },
                isTitleVisible: true,
                experience: {
                    localExperience: [
                        {
                            position: "test",
                            years: 1.5,
                        }
                    ],
                    totalExperience: 2.5
                },
                contactInfo: {
                    residence: "Israel",
                    isResidenceVisible: true,
                    birthday: date3,
                    isBirthdayVisible: true,
                    phone: "899999999",
                    isPhoneVisible: true,
                    email: "test1@mail.com",
                    isEmailVisible: true,
                    linkedIn: "http://sdad.com",
                    isLinkedInVisible: true
                },
                isContactInfoVisible: true,
                description: {
                    about: "asdsad",
                    preferableArea: [
                        "asdas"
                    ],
                    position: "asdad",
                    salaryFrom: 3000,
                    salaryTo: 8000
                },
                education: [
                    {
                        schoolInstitutionName: "asda",
                        degree: "asd",
                        graduationYear: "asd",
                        location: "ad",
                        details: "asda",
                        isEducationVisible: true
                    }
                ],
                skills: [
                    {
                        skillName: "dsada",
                    }
                ],
                projectJobs: [
                    {
                        company: "sadasd",
                        isCompanyVisible: true,
                        position: "dasda",
                        backEndFrontEndRatio: "50",
                        startDate: date1,
                        endDate: date2,
                        projectDescription: "asdasd"
                    }
                ],
                achivements: [
                    "asdad"
                ],
                certifications: [
                    "asda"
                ],
                languages: [
                    "asd"
                ],
                footerLinks: [
                    "asd"
                ],
                isFooterLinkVisible: true,
                footerInfo: [
                    "asd"
                ],
                isFooterInfoVisible: true,
                isActive: true,
                cvStyle: ""
            },
            // this.props.data.JobSeekerReducer.

            timeout: 5000
        }).then( (response)=> {

            let temp = [...this.state.cvList];

            temp.push({
                cvId: response.data.cvId,
                cvName: response.data.cvName,
                viewCount: 0
            });

            this.setState({
                ...this.state,
                cvList:temp,
                cvData: {
                    ...this.state.cvData,
                    [response.data.cvId]: null
                },
                chosenCvs: {
                    ...this.state.chosenCvs,
                    [response.data.cvId]: false
                }
            });
            this.setState({...this.state,dialogLoading: false});
            // console.log(response.data);
            // console.log(response.status);
            // console.log(response.statusText);
            // console.log(response.headers);
            // console.log(response.config);
        }).catch( (error) => {
            this.setState({...this.state,dialogLoading: false});
            // handle error
            console.log(error);
        })
    };






    componentWillMount() {
        // if (!localStorage.getItem('tooken') && !localStorage.getItem('jsData')) {
        //     window.location.href = '/';
        // }
    }

    componentDidMount() {

        this.getUserData();

    };

    getUserData = () => {

        this.setState({
            ...this.state,
            personData: {
                ...JSON.parse(Buffer.from(localStorage.getItem('jsData'),'base64').toString('ascii'))
            }
        })
    };

    setLoadingDialog = (on) => {
        this.setState({
            ...this.state,
            dialogLoading: !!on
        })
    };

    setErrorSnackBar = (message) => {
        this.setState({
            ...this.state,
            errorType: message === 'success'
            ||message === 'error'
            ||message === 'info'
            ||message === 'warning'?message:'error',
            errorMessage: message?message:null
        })
    };

    handleChange = name => e => {
        this.setState({
            ...this.state,
            personData: {
                ...this.state.personData,
                [name]: e.target.value
            }
        })
    };

    saveChanges = () => {
        this.setLoadingDialog(true);
        let i = 0;
        let error = false;
        if (this.state.personData) {
            for (let key in this.state.personData){
                // console.log(key);
                if (!this.state.personData[key] || this.state.personData[key] === '') error = true;
                i++;
            }

            if (i===4 && error === false) {

                axios({
                    method:'put',
                    url:'/jobSeeker/profile',
                    baseURL: BASE_URL,
                    headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
                    data:{
                        firstName: this.state.personData.firstName,
                        lastName: this.state.personData.lastName,
                        email: this.state.personData.email,
                        phone: this.state.personData.phone,
                    },
                    timeout: 10000
                }).then( (response) => {
                    const personData = {
                        firstName: response.data.jobSeeker.firstName,
                        lastName: response.data.jobSeeker.lastName,
                        email: response.data.jobSeeker.email,
                        phone: response.data.jobSeeker.phone,
                    };
                    localStorage.setItem('jsData', Buffer.from(JSON.stringify(personData))
                        .toString('base64'));

                    this.setLoadingDialog();
                    this.setErrorSnackBar('success');
                }).catch( (error) => {
                    this.setLoadingDialog();
                    this.setErrorSnackBar(error.toString());
                })
            }else {
                this.setLoadingDialog();
                this.setErrorSnackBar('you should fill all fields');
            }
        }else {
            this.setLoadingDialog();
            this.setErrorSnackBar('you should fill all fields');
        }
    };









    getCvs = () =>{
        this.setRefreshButton();
        this.getProfile().then((response) => {
            this.setState({
                ...this.state,
                cvList: response.data.cvList,
            });
            let cvData = {};
            let chosenCvs = {};
            response.data.cvList.map(item => {
                cvData[item.cvId]=null;
                chosenCvs[item.cvId]=false;
                return false
            });

            this.setState({
                ...this.state,
                cvData: {
                    ...this.state.cvData,
                    ...cvData
                },
                chosenCvs:{
                    ...this.state.chosenCvs,
                    ...chosenCvs
                }
            });

        }).catch((error) => {
            this.setRefreshButton(true);
            this.setState({
                ...this.state,
                errorMessage: error,
            });
        });

    };

    handleOpenPanel = id => (event, expanded) => {
        // console.log(id+'  '+expanded);
        if (expanded || expanded == null) {
            this.setRefreshButton();
            this.getCv(id).then((response) => {
                this.setState({
                    ...this.state,
                    cvData: {
                        ...this.state.cvData,
                        [id]: response.data
                    }
                });
                // console.log(response);
            }).catch((error) => {
                this.setRefreshButton(true);
                this.setState({
                    ...this.state,
                    errorMessage: error,
                });
            });

        }
    };

    handleChecked = name => event => {
        this.setState({...this.state,
            chosenBar:{...this.state.chosenBar,number:event.target.checked?this.state.chosenBar.number+1:this.state.chosenBar.number-1},
            chosenCvs:{...this.state.chosenCvs,[name]: event.target.checked} });
        // console.log(event.target.checked);
        // console.log(name);


    };

    selectAll = (type) => {
        let t = 0;
        let temp={};
        Object.keys(this.state.chosenCvs).map(item=>{
            temp[item]=type === 'select';
            return type === 'select'?t++:0;
        });
        this.setState({
            ...this.state,
            chosenCvs:{
                ...temp
            },
            chosenBar:{
                ...this.state.chosenBar,
                number: t
            }
        })
    };

    setRefreshButton = (on) => {
        this.setState({
            ...this.state,
            refreshButton: on?on:false
        })
    };

    deleteCv = () => {
        this.setState({
            ...this.state,
            chosenBar:{
                ...this.state.chosenBar,
                number: 0
            },
            dialogDelete: false,
            dialogLoading: true});

        let arr = [];
        Object.entries(this.state.chosenCvs).map(item =>{
            return item[1]?arr.push(item[0]):false;
        });

        let promisesArray = arr.map(item => {
            return new Promise((resolve, reject) => {
                this.delete(item).then(request=>{
                    resolve(request);
                }).catch(error=>{
                    reject(error);
                });
            })
        });

        Promise.all(promisesArray)
            .then((results)=> {
                // console.log(results);
                this.selectAll('unselect');


                this.getProfile().then((response) => {
                    this.setState({
                        ...this.state,
                        cvList: response.data.cvList,
                    });
                    let cvData = {};
                    let chosenCvs = {};
                    response.data.cvList.map(item => {
                        cvData[item.cvId]=null;
                        chosenCvs[item.cvId]=false;
                        return false
                    });

                    this.setState({
                        ...this.state,
                        cvData: {
                            ...cvData
                        },
                        chosenCvs:{
                            ...chosenCvs
                        }
                    });
                    this.setState({...this.state,dialogLoading: false});

                }).catch((error) => {
                    this.selectAll('unselect');
                    this.setState({...this.state,dialogLoading: false});
                    this.setState({
                        ...this.state,
                        errorMessage: error,
                    });
                });
            })
            .catch((error) => {
                this.selectAll('unselect');
                this.setState({...this.state,dialogLoading: false});
                console.log(error)
            });





    };
    render() {

        // const Edit = props => <Link to={{pathname:'/job_seeker/edit', state:{data:this.props.data.JobSeekerReducer}}} />;

        const { classes } = this.props;

        const drawerL = (
            <div>

                <List
                    component="nav"
                    disablePadding={true}
                >
                    {/*<Link to={{pathname:'/job_seeker/edit', state:{data:this.props.data.JobSeekerReducer}}}>*/}
                    {/*sss*/}
                    {/*</Link>*/}

                    <ListItem
                        button
                        disableRipple={true}
                        component={Link}
                        to={{pathname:'/job_seeker/manageCVs', state:{
                                data:this.props.data.JobSeekerReducer,
                                // prevPath: this.props.location.pathname
                            }}}
                        disableGutters={true}
                    >
                        <ListItemIcon>
                            <PersonIcon/>
                        </ListItemIcon>
                        <ListItemText disableTypography={true}>
                            Manage CVs
                        </ListItemText>
                    </ListItem>
                    <ListItem
                        button
                        disableRipple={true}
                        component={Link}
                        // to={{pathname:'/job_seeker/statistic', state:{
                        //         data:this.props.data.JobSeekerReducer,
                        //         // prevPath: this.props.location.pathname
                        //     }}}
                        disableGutters={true}
                    >
                        <ListItemIcon>
                            <InsertChartIcon />
                        </ListItemIcon>
                        <ListItemText disableTypography={true}>
                            Statistics
                        </ListItemText>
                    </ListItem>

                    <ListItem
                        button
                        disableRipple={true}
                        disabled={true}
                        component={Link}
                        to={{pathname:'/job_seeker/settings', state:{
                                data:this.props.data.JobSeekerReducer,
                                // prevPath: this.props.location.pathname
                            }}}
                        disableGutters={true}
                    >
                        <ListItemIcon>
                            <SettingsIcon />
                        </ListItemIcon>
                        <ListItemText disableTypography={true}>
                            Settings
                        </ListItemText>
                    </ListItem>


                </List>
            </div>
        );

        return (
            <React.Fragment>

                {localStorage.getItem('tooken') && localStorage.getItem('jsData') &&

                <div style={{display: 'flex'}}>

                    <CssBaseline/>
                    {/*<AppBar position="fixed">*/}
                    {/*<Toolbar>*/}

                    {/*/!*{toolbarMenu(menu)}*!/*/}

                    {/*<img style={{*/}
                    {/*height: '35px'*/}
                    {/*}} src={img.logoa2x} alt="logo"/>*/}
                    {/*</Toolbar>*/}
                    {/*</AppBar>*/}

                    <React.Fragment>
                        <Hidden smUp implementation="css">
                            <SwipeableDrawer
                                keepMounted={true}
                                hysteresis={.60}
                                swipeAreaWidth={20}
                                disableEnforceFocus={true}
                                disableAutoFocus={true}
                                disableRestoreFocus={true}
                                disableSwipeToOpen={false}
                                container={this.props.container}
                                variant="temporary"
                                open={this.state.mobileOpenL}
                                onClose={this.handleDrawerToggleL}
                                onOpen={this.handleDrawerToggleL}
                            >
                                {drawerL}

                            </SwipeableDrawer>
                        </Hidden>
                        <Hidden xsDown implementation="css">
                            <Drawer
                                variant="permanent"
                                open
                            >
                                {drawerL}
                            </Drawer>
                        </Hidden>
                    </React.Fragment>


                    <LoadingDialog state={this.state.dialogLoading}/>


                    <main className={classes.content}>

                        <div style={{
                            position: 'absolute',
                            width: '100%',
                            display: 'flex',
                            justifyContent: 'center'
                        }}>

                            {this.state.chosenBar.number > 0 &&
                            <div style={{
                                position: 'fixed',
                                display: 'flex',
                                padding: 5,
                                justifyContent: 'space-between',
                                color: '#01415C',
                                alignItems: 'center',
                                fontWeight: 700,
                                fontSize: '1.1em',
                                zIndex: 1300,
                                minWidth: '300px',
                                marginTop: '.5em',
                                width: '55%',
                                height: '3em',
                                background: '#e9eefd',
                                borderRadius: 3,
                                boxShadow: '0px 0px 5px 0px rgba(0,0,0,0.25)'
                            }}>
                                <div>
                                    Selected: {this.state.chosenBar.number}
                                    <Button
                                        size={"small"}
                                        onClick={() => this.selectAll('select')}
                                    >
                                        all
                                    </Button>
                                    <IconButton
                                        onClick={() => this.setState({...this.state, dialogDelete: true})}
                                        style={{
                                            color: '#01415C',
                                        }}>
                                        <DeleteForever/>
                                    </IconButton>
                                    <Dialog
                                        open={this.state.dialogDelete}
                                        onClose={() => this.setState({...this.state, dialogDelete: false})}
                                        classes={{
                                            paper: classes.paper
                                        }}
                                    >
                                        <DialogContent>
                                            <DialogContentText>
                                                Delete forever?
                                            </DialogContentText>
                                        </DialogContent>
                                        <DialogActions>
                                            <Button onClick={() => this.setState({...this.state, dialogDelete: false})}
                                                    color="primary">
                                                no
                                            </Button>
                                            <Button onClick={() => {
                                                this.deleteCv()
                                            }}
                                                    color="primary"
                                                    autoFocus
                                            >
                                                delete
                                            </Button>
                                        </DialogActions>
                                    </Dialog>
                                </div>
                                <div>
                                    <IconButton
                                        onClick={() => this.selectAll('unselect')}
                                        style={{
                                            color: '#01415C',
                                        }}>
                                        <Close/>
                                    </IconButton>
                                </div>
                            </div>}


                        </div>


                        <Fab
                            color="primary"
                            aria-label="Open palette drawer"
                            onClick={this.handleDrawerToggleL}
                            className={classes.menuButtonEditor}
                        >
                            <Icon>menu</Icon>
                        </Fab>


                        {/*<div style={{*/}
                            {/*margin: '1em',*/}
                            {/*display: 'flex',*/}
                            {/*justifyContent: 'flex-end'*/}
                        {/*}}>*/}
                            {/*/!*<Button onClick={()=>this.registration()}>registration</Button>*!/*/}
                            {/*/!*<Button onClick={()=>this.login()}>login</Button>*!/*/}
                            {/*/!*<Button onClick={()=>this.getProfile()}>profile</Button>*!/*/}
                            {/*<Button*/}
                                {/*className={classes.margin}*/}
                                {/*variant={"contained"}*/}
                                {/*color={"secondary"}*/}
                                {/*mini={true}*/}
                                {/*onClick={() => this.addCV()}>addCv</Button>*/}

                            {/*<Button*/}
                                {/*className={classes.margin}*/}
                                {/*variant={"contained"}*/}
                                {/*color={"primary"}*/}
                                {/*mini={true}*/}
                                {/*// style={{padding: '0 50px 0 50px'}}*/}
                                {/*component={Link}*/}
                                {/*to={{pathname: '/job_seeker/edit'}}>Create new CV</Button>*/}
                        {/*</div>*/}


                        <div style={{marginTop: '.5em'}}>

                            {this.state.refreshButton && !this.state.personData &&

                            <div
                                className='fadeIn'
                                style={{
                                    display:'flex',
                                    alignItems:'center',
                                    justifyContent:'center',
                                    height:'10vh'
                                }}
                            >
                                <IconButton
                                    onClick={this.getCvs}
                                >
                                    <Refresh/>
                                </IconButton>
                            </div>
                            }


                            {this.state && this.state.personData &&
                                <div
                                    style={{
                                        margin:'1em',
                                        // width:'85%'
                                    }}
                                    className='fadeIn'
                                >
                                    <Paper>
                                    <ValidatorForm
                                        ref="jsSettingsForm"
                                        onSubmit={this.saveChanges}
                                    >
                                    <InputValidateSingleRow
                                        helperText={' '}
                                        border={false}
                                        boxShadow={'none'}
                                        labelText={'first name'}
                                        value={this.state.personData.firstName}
                                        onChange={this.handleChange('firstName')}
                                        validators={['required', 'isString','matchRegexp:^[A-Z]{1}[A-z]{2,14}$']}
                                        errorMessages={['this field is required', 'not valid', 'not valid']}
                                    />
                                    <InputValidateSingleRow
                                        helperText={' '}
                                        border={false}
                                        boxShadow={'none'}
                                        labelText={'last name'}
                                        value={this.state.personData.lastName}
                                        onChange={this.handleChange('lastName')}
                                        validators={['required', 'isString','matchRegexp:^[A-Z]{1}[A-z]{2,14}$']}
                                        errorMessages={['this field is required', 'not valid', 'not valid']}
                                    />
                                    <InputValidateSingleRow
                                        helperText={' '}
                                        border={false}
                                        boxShadow={'none'}
                                        labelText={'email'}
                                        value={ this.state.personData.email}
                                        onChange={this.handleChange('email')}
                                        validators={['required', 'isEmail']}
                                        errorMessages={['this field is required', 'email is not valid']}
                                    />
                                    <InputValidateSingleRow
                                        helperText={' '}
                                        border={false}
                                        boxShadow={'none'}
                                        labelText={'phone'}
                                        value={this.state.personData.phone}
                                        onChange={this.handleChange('phone')}
                                        validators={['required','matchRegexp:^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\\s\\./0-9]*$']}
                                        errorMessages={['this field is required', 'phone is not valid','no valid']}
                                    />
                                    <div style={{
                                        display:'flex',
                                        justifyContent:'flex-end',
                                        alignItem:'center',
                                        flexGrow:1,
                                    }}>
                                        <Button
                                            style={{margin: 4, width:'9em', height:'2.3em'}}
                                            size={"small"}
                                            type={"submit"}
                                            variant={"contained"}
                                            color={"primary"}
                                            // onClick={this.saveChanges}
                                        >
                                            <Save/>
                                            save
                                        </Button>
                                    </div>
                                    </ValidatorForm>
                                    </Paper>
                                </div>
                            }
                        </div>


                        {!this.state.personData && !this.state.refreshButton &&
                        <div className='fadeIn' style={{display: 'flex', justifyContent: 'center'}}>
                            <div className="dot-loader"></div>
                            <div className="dot-loader"></div>
                            <div className="dot-loader"></div>
                        </div>}

                        <ErrorSnackBar
                            open={!!this.state.errorMessage}
                            variant={this.state.errorType?this.state.errorType:'error'}
                            autoHideDuration={5000}
                            onClose={() => this.setState({...this.state, errorMessage: null})}
                            message={this.state.errorMessage?this.state.errorMessage.toString():''}
                        />

                        {/*{this.state.errorMessage &&*/}
                        {/*<Snackbar*/}
                        {/*anchorOrigin={{*/}
                        {/*vertical: 'bottom',*/}
                        {/*horizontal: 'left',*/}
                        {/*}}*/}
                        {/*open={true}*/}
                        {/*onClose={() => this.setState({...this.state, errorMessage: null})}*/}
                        {/*autoHideDuration={6000}*/}
                        {/*ContentProps={{*/}
                        {/*// 'aria-describedby': 'message-id',*/}
                        {/*}}*/}
                        {/*message={*/}
                        {/*<div style={{*/}
                        {/*display: 'flex',*/}
                        {/*alignItems: 'center',*/}
                        {/*}}>*/}
                        {/*<ErrorIcon style={{margin: '0 20px 0 0'}}/>*/}
                        {/*<span id="message-id">{this.state.errorMessage.toString()}</span>*/}
                        {/*</div>*/}
                        {/*}*/}
                        {/*/>}*/}

                        <div style={{padding: '1em'}}>
                          <ActivateAdvertising/>
                        </div>

                    </main>

                    <React.Fragment>
                        {/*<Hidden smUp implementation="css">*/}
                        {/*<SwipeableDrawer*/}
                        {/*container={this.props.container}*/}
                        {/*variant="temporary"*/}
                        {/*anchor='right'*/}
                        {/*open={this.state.mobileOpenR}*/}
                        {/*onClose={this.handleDrawerToggleR}*/}
                        {/*onOpen={this.handleDrawerToggleR}*/}
                        {/*>*/}
                        {/*{drawerR}*/}
                        {/*</SwipeableDrawer>*/}
                        {/*</Hidden>*/}
                        <Hidden xsDown implementation="css">
                            <Drawer
                                variant="permanent"
                                anchor="right"
                                open
                            >

                            </Drawer>
                        </Hidden>
                    </React.Fragment>

                </div>
                }
            </React.Fragment>
        );
    }

}

export default withStyles(styles)(SettingsComponent)
