import React, {Component, Fragment} from 'react';

import Save from '@material-ui/icons/Save';
import Close from '@material-ui/icons/Close';
import CheckBoxOutlineBlankOutlined from '@material-ui/icons/CheckBoxOutlineBlankOutlined';
import CheckBoxOutlined from '@material-ui/icons/CheckBoxOutlined';
import RadioButtonUnchecked from '@material-ui/icons/RadioButtonUnchecked';
import RadioButtonChecked from '@material-ui/icons/RadioButtonChecked';
import DeleteForever from '@material-ui/icons/DeleteForever';
import Smartphone from '@material-ui/icons/Smartphone';
import Email from '@material-ui/icons/Email';
import LocationCity from '@material-ui/icons/LocationCity';
import Person from '@material-ui/icons/Person';
import LibraryBooks from '@material-ui/icons/LibraryBooks';
import ArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import PersonIcon from '@material-ui/icons/Person';
import SettingsIcon from '@material-ui/icons/Settings';
import InsertChartIcon from '@material-ui/icons/InsertChart';
import Search from '@material-ui/icons/Search';
import Edit from '@material-ui/icons/Edit';


import { connect} from "react-redux";
import {Link, Redirect, Route} from "react-router-dom";
import {
    ActivateAdvertising, AddButton,
    DrawerListItem,
    ErrorSnackBar,
    InputChips,
    InputSingleRow,
    LoadingDialog, RefreshButton
} from "../../theme/cvBankForms";
import {Button, DialogTitle, Fab, IconButton, withStyles} from "@material-ui/core";
import {img} from "../../assets/_img";
import Hidden from "@material-ui/core/Hidden";
import Drawer from "@material-ui/core/Drawer";
import Icon from "@material-ui/core/Icon";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import axios from "axios";
import {BASE_URL} from "../../config";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import Checkbox from "@material-ui/core/Checkbox";
import TemplateComponent from "../../core/components/template/Template.component";
import Chip from "@material-ui/core/Chip";
import Dialog from "@material-ui/core/Dialog";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import GridList from "@material-ui/core/GridList";
import Tooltip from "@material-ui/core/Tooltip";
import Add from "../../theme/cvBankForms";


const styles = theme => ({
    menuButtonEditor: {
        position: 'fixed',
        marginTop: 'calc(100vh - 16.5em)',
        marginLeft: 'calc(100% - 8em)',
        zIndex: 1000,
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    menuButtonPalette: {
        position: 'fixed',
        marginTop: 'calc(100vh - 16.5em)',
        marginLeft: 'calc(100% - 7em)',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },

    content: {
        position:'relative',
        flexGrow: 1,
    },
    paper:{
        background: 'white !important',
    },
    expansionPanel: {
        boxShadow: 'none'
    },
    margin:{
        margin:'0 4px 0 4px'
    }

});

class HiringManageCVsComponent extends Component {
    state = {
        mobileOpenL: false,
        mobileOpenR: false,
        snackbarIsOpen: false,
        errorMessage: null,
            errorType:'error',
        getFoldersError:false,
        dialogLoading: false,
        dialogDeleteFolder:false,
        dialogAddToFolder:null,
        dialogEditFolder:null,
        dialogCreateNewFolder:null,
        prevFolderName:'',
        newFolderName:'',
        cutDialog:null,
        chosenBar: {
            number: 0,
            open: false
        },
        chosenCVs:null,
        isOpen: {
            1: true,
            2: false,
            3: false,
            4: false,
            5: false,
            6: false,
            7: false,
            8: false,
            9: false,
            10: false,
            11: false,
            12: false,
            menu: false
        },
        folders:null,
        currentFolder:'',
    };

    toggleOpen = (number) => {
        this.setState(state => ({ ...state, isOpen: {
                ...this.state.isOpen,
                [number]: !this.state.isOpen[number]
            } }));
    };

    handleDrawerToggleL = () => {
        this.setState(state => ({
            ...this.state,
            mobileOpenL: !state.mobileOpenL }));
    };

    componentWillMount() {
        this.getUserFolders();
    }

    setErrorSnackBar = (message, type) => {
        this.setState({
            ...this.state,
            errorType: type?type:'error',
            errorMessage: message?message:null
        })
    };

    setDialogLoading = (type) => {
        this.setState({...this.state,dialogLoading:!!type})
    };

    deleteFolder = (name) => {
        this.setDialogLoading(true);
        axios({
            method:'delete',
            url:'/company/folder',
            baseURL: BASE_URL,
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            params:{
              folderName:name
            },
            timeout: 10000,
        }).then((response) => {
            this.setState({
                ...this.state,
                currentFolder:'',
                dialogDeleteFolder:false
            });
            this.setDialogLoading();
            this.setErrorSnackBar('deleted', 'success');
            this.getUserFolders();
        }).catch((error) => {
            this.setDialogLoading();
            this.setErrorSnackBar('something wrong');
            // this.setErrorSnackBar('can`t get your folders');
        })
    };

    getUserFolders = () =>{
        this.setDialogLoading(true);
        axios({
            method:'get',
            url:'/company/myFolders',
            baseURL: BASE_URL,
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 10000,
        }).then((response) => {
            // console.log(response.data.folders);
            let ids = {};
            response.data.folders.map((e1,i1)=>{
                ids[e1.folder.folderName] = {};
                   e1.folder.cvList.map((e2,i2)=>{
                       ids[e1.folder.folderName][e2.cvId] = false
               })
            });

            // console.log(ids);

            this.setState({
                ...this.state,
                getFoldersError:false,
                folders:response.data.folders,
                chosenCVs:ids
            });
            this.setDialogLoading();
            // this.setErrorSnackBar('saved', 'success');
        }).catch((error) => {
            this.setState({
                ...this.state,
                getFoldersError:true,
                errors:{
                    ...this.state.errors,
                    getUserFolders:true,
                },
                folders:[
                ]
            });
            this.setDialogLoading();
            this.setErrorSnackBar(error.toString());
            // this.setErrorSnackBar('can`t get your folders');
        })
    };

    deleteCV = (cvId, folderName) => {
        let req = axios({
            method:'delete',
            url:'company/cvToFolder',
            baseURL: BASE_URL,
            params: {
                cvId: cvId,
                folderName:folderName
            },
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 15000
        });

        return req;
    };

    deleteChosenCv = () => {
        this.setState({
            ...this.state,
            chosenBar:{
                ...this.state.chosenBar,
                number: 0
            },
            dialogLoading: true});

        let arr = [];
        Object.entries(this.state.chosenCVs[this.state.currentFolder]).map(item =>{
            return item[1]?arr.push(item[0]):false;
        });

        let promisesArray = arr.map(item => {
            return new Promise((resolve, reject) => {
                this.deleteCV(item, this.state.currentFolder).then(request=>{
                    resolve(request);
                }).catch(error=>{
                    reject(error);
                });
            })
        });

        Promise.all(promisesArray)
            .then((results)=> {
                // console.log(results);
                this.selectAll('unselect');

                this.getUserFolders();
                this.setState({...this.state,dialogLoading: false});
                this.setErrorSnackBar('deleted','success');
            })
            .catch((error) => {
                this.selectAll('unselect');
                this.setState({...this.state,dialogLoading: false});
                this.setErrorSnackBar(error.toString())
            });


    };

    cutAllToFolder = (name) => {
        let temp=[];
        this.setDialogLoading(true);
        Object.entries(this.state.chosenCVs[this.state.currentFolder]).map(item=>{
            return item[1]?temp.push(item[0]):false;

        });
        this.setState({
            ...this.state,
            chosenBar:{
                ...this.state.chosenBar,
                number: 0
            }});

        let promisesArray = temp.map(item => {
            return new Promise((resolve, reject) => {
                this.saveToFolder(item, name).then(request=>{
                    this.deleteCV(item, this.state.currentFolder).then(request=>{
                        resolve(request);
                    }).catch(error=>{
                        reject(error);
                    });
                    // resolve(request);
                }).catch(error=>{
                    reject(error);
                });
            })
        });

        let promisesArray2 = temp.map(item => {
            return new Promise((resolve, reject) => {
                this.deleteCV(item, this.state.currentFolder).then(request=>{
                    resolve(request);
                }).catch(error=>{
                    reject(error);
                });
            })
        });

        // console.log(promisesArray);

        Promise.all(promisesArray)
            .then((results)=> {

                this.selectAll('unselect');
                this.setErrorSnackBar('succsess','success');
                this.setState({...this.state, cutDialog: null});

                this.getUserFolders();
                // Promise.all(promisesArray2).then(res=>{
                //     this.selectAll('unselect');
                //     this.setErrorSnackBar('success','success');
                //     this.setState({...this.state, dialogAddToFolder: null});
                //
                //     this.getUserFolders();
                // }).catch((error) => {
                //     this.selectAll('unselect');
                //     this.setDialogLoading();
                //     this.setErrorSnackBar(error.toString());
                //     // console.log(results);
                // })

            }).catch((error) => {
            this.selectAll('unselect');
            this.setDialogLoading();
            this.setErrorSnackBar(error.toString());
        });

    };


    saveAllToFolder = (name) => {
        let temp=[];
        this.setDialogLoading(true);
        Object.entries(this.state.chosenCVs[this.state.currentFolder]).map(item=>{
            return item[1]?temp.push(item[0]):false;

        });
        this.setState({
            ...this.state,
            chosenBar:{
                ...this.state.chosenBar,
                number: 0
            }});

        let promisesArray = temp.map(item => {
            return new Promise((resolve, reject) => {
                this.saveToFolder(item, name).then(request=>{
                    resolve(request);
                }).catch(error=>{
                    reject(error);
                });
            })
        });

        // console.log(promisesArray);

        Promise.all(promisesArray)
            .then((results)=> {
                // console.log(results);
                this.selectAll('unselect');
                this.setErrorSnackBar('saved','success');
                this.setState({...this.state, dialogAddToFolder: null});

                this.getUserFolders();

            }).catch((error) => {
            this.selectAll('unselect');
            this.setDialogLoading();
            this.setErrorSnackBar(error.toString());
        });
    };

    saveToFolder = (cvId,name) => {
        let req = axios({
            method:'post',
            url:'/company/cvToFolder',
            baseURL: BASE_URL,
            headers: {'Authorization': JSON.parse(localStorage.getItem('tooken')),'Content-Type': 'application/json'},
            timeout: 10000,
            params: {
                cvId: cvId,
                folderName: name
            },
        });
        return req
    };

    getAge =(DOB)=> {
        let today = new Date();
        let birthDate = new Date(DOB);
        let age = today.getFullYear() - birthDate.getFullYear();
        let m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age = age - 1;
        }

        return age;
    };

    handleChecked = id => event => {
        // console.log(id);
        // console.log(event.target.checked);
        this.setState({...this.state,
            chosenBar:{...this.state.chosenBar,number:event.target.checked?this.state.chosenBar.number+1:this.state.chosenBar.number-1},
            chosenCVs:{...this.state.chosenCVs,
                [this.state.currentFolder]: {
                ...this.state.chosenCVs[this.state.currentFolder],
                    [id]: event.target.checked
                }
            } });
    };

    selectAll = (type) => {
        let t = 0;
        let temp={

        };
        if (type === 'select') {
            Object.keys(this.state.chosenCVs[this.state.currentFolder]).map(item => {
                temp[item] = true;
                return t++;
            });
            // console.log(temp);
            this.setState({
                ...this.state,
                chosenCVs: {
                    ...this.state.chosenCVs,
                    [this.state.currentFolder]: {
                        ...temp
                    }
                },
                chosenBar: {
                    ...this.state.chosenBar,
                    number: t
                }
            })
        }else {
            Object.entries(this.state.chosenCVs).map(item => {
                temp[item[0]] = {};
                Object.keys(item[1]).map(item2=>{
                   return temp[item[0]][item2]=false
                });
                return 0;
            });
            // console.log(temp);
            this.setState({
                ...this.state,
                chosenCVs:temp,
                chosenBar: {
                    ...this.state.chosenBar,
                    number: t
                }
            })
        }
    };

    createFolder = (name,previousName) => {
        if (name && name !== '') {
            this.setDialogLoading(true);
            if (previousName) {
                axios({
                    method: 'post',
                    url: '/company/folder',
                    baseURL: BASE_URL,
                    headers: {
                        'Authorization': JSON.parse(localStorage.getItem('tooken')),
                        'Content-Type': 'application/json'
                    },
                    timeout: 10000,
                    params: {
                        folderName: previousName,
                        newFolderName: name,
                    },
                }).then((response) => {
                    this.setDialogLoading();
                    this.setErrorSnackBar('edited', 'success');
                    this.getUserFolders();
                }).catch((error) => {
                    if (error.response.status === 500) {
                        this.setDialogLoading();
                        this.setErrorSnackBar(error.toString());
                    } else {
                        this.setDialogLoading();
                        this.setErrorSnackBar(error.toString());
                    }
                })
            }else {
                axios({
                    method: 'put',
                    url: '/company/folder',
                    baseURL: BASE_URL,
                    headers: {
                        'Authorization': JSON.parse(localStorage.getItem('tooken')),
                        'Content-Type': 'application/json'
                    },
                    timeout: 10000,
                    params: {
                        folderName: name,
                    },
                }).then((response) => {
                    this.setDialogLoading();
                    this.setErrorSnackBar('created', 'success');
                    this.getUserFolders();
                }).catch((error) => {
                    if (error.response.status === 500) {
                        this.setDialogLoading();
                        this.setErrorSnackBar('this name is already using', 'warning');
                    } else {
                        this.setDialogLoading();
                        this.setErrorSnackBar(error.toString());
                    }
                })
            }
        }else {
            this.setErrorSnackBar('type a folder name','warning');
        }
    };



    render() {
        const { classes } = this.props;

        const drawerL = (
            <div>

                <List
                    component="nav"
                    disablePadding={true}
                >

                    <ListItem
                        button
                        disableRipple={true}
                        component={Link}
                        to={{pathname:'/hiring/search', state:{
                                data:this.props.data.HiringReducer,
                                // prevPath: this.props.location.pathname
                            }}}
                        disableGutters={true}
                    >
                        <ListItemIcon>
                            <Search />
                        </ListItemIcon>
                        <ListItemText disableTypography={true}>
                            Search CVs
                        </ListItemText>
                    </ListItem>

                    <ListItem
                        button
                        disableRipple={true}
                        component={Link}
                        to={{pathname:'/hiring/manageCVs', state:{
                                data:this.props.data.HiringReducer,
                                // prevPath: this.props.location.pathname
                            }}}
                        disableGutters={true}
                    >
                        <ListItemIcon>
                            <SettingsIcon />
                        </ListItemIcon>
                        <ListItemText disableTypography={true}>
                            Settings
                        </ListItemText>
                    </ListItem>

                    <ListItem
                        button
                        disableRipple={true}
                        disabled={true}
                        component={Link}
                        to={{pathname:'/hiring/manageCVs', state:{
                                data:this.props.data.HiringReducer,
                                // prevPath: this.props.location.pathname
                            }}}
                        disableGutters={true}
                    >
                        <ListItemIcon>
                            <PersonIcon/>
                        </ListItemIcon>
                        <ListItemText disableTypography={true}>
                            Manage CVs
                        </ListItemText>
                    </ListItem>
                    <div
                        className='fadeIn'
                        style={{
                            marginLeft: "5%"
                        }}
                    >

                        <DrawerListItem
                            titleText={'Your folders:'}
                            isOpen={this.state.isOpen[1]}
                            onClick={()=>this.toggleOpen(1)}
                        >
                            <div
                                className='fadeIn'
                                style={{
                                    marginLeft: "5%"
                                }}
                            >
                                <RefreshButton isVisible={this.state.getFoldersError} onClick={this.getUserFolders}/>
                            {this.state.folders && this.state.folders.map((item,index)=>(
                                <DrawerListItem
                                    style={{fontSize:'.8em'}}
                                titleText={item.folder.folderName}
                                    isOpen={this.state.currentFolder === item.folder.folderName}
                                    onClick={()=> {
                                            this.setState({
                                                ...this.state,
                                                currentFolder: item.folder.folderName
                                            },this.selectAll)
                                    }}

                                >
                                    <Tooltip title="Change name (not working yet)">
                                    <IconButton
                                        color={"primary"}
                                        onClick={()=>this.setState({...this.state,
                                            dialogEditFolder:true,prevFolderName:item.folder.folderName})}
                                    >
                                        <Edit/>
                                    </IconButton>
                                    </Tooltip>
                                    <Tooltip title="Share (not working yet)">
                                    <IconButton>
                                        <i style={{color:'white', fontSize:'1.3rem'}} className="fas fa-share-alt"/>
                                    </IconButton>
                                    </Tooltip>
                                    <Tooltip title="Delete folder">
                                    <IconButton
                                        color={"primary"}
                                        onClick={()=>this.setState({
                                            ...this.state,
                                            dialogDeleteFolder:true
                                        })}
                                    >
                                        <DeleteForever/>
                                    </IconButton>
                                    </Tooltip>
                                </DrawerListItem>
                            ))}

                                <AddButton
                                    tooltipTitle='Create new folder'
                                    onClick={()=>this.setState({...this.state,dialogCreateNewFolder:true})}
                                    style={{float:'right', fontSize:'1rem !important'}}
                                    />
                            </div>
                        </DrawerListItem>
                        {/*<DrawerListItem*/}
                            {/*titleText={'Saved CVs'}*/}
                            {/*isOpen={this.state.isOpen[2]}*/}
                            {/*onClick={()=>this.toggleOpen(2)}*/}
                        {/*>*/}
                        {/*</DrawerListItem>*/}
                    </div>


                </List>
            </div>
        );

        const skillsOnPanel = (item) => {
            let temp = [];
            let temp2 =[];

            // item.skills.map((item1,index)=>{
            //     this.state.searchReq.skills.forEach(item2=>{
            //         return item2.skillName===item1.skillName?
            //             temp.push(item1.skillName)
            //             :false
            //     })
            // });
            // if (this.state.searchReq.skills.length === 0) {
                item.skills.map((item1,index)=>{
                    return temp2.push(item1.skillName)
                });
            // }else {
            //     item.skills.map((item1, index) => {
            //         return this.state.searchReq.skills.forEach(item2 => {
            //             return item2.skillName !== item1.skillName && (!temp.includes(item1.skillName) && !temp2.includes(item1.skillName)) ?
            //                 temp2.push(item1.skillName)
            //                 : false
            //         })
            //     });
            // }

            // console.log(temp);
            // console.log(temp2);

            // temp = temp.map(item=><Chip color={"primary"} variant={"outlined"} label={item}/>);
            temp2 = temp2.map(item=><Chip color={"secondary"} variant={"outlined"} label={item}/>);

            // if (temp.length < 5 && temp2.length !== 0) {
            //     for (let i = 0; (temp.length < 6 && i<temp2.length) || i<temp2.length; i++){
            //         temp.push(temp2[i])
            //     }
            // }


            // return this.state.searchReq.skills.length===0?temp2.slice(0,6):temp
            return temp2.slice(0,6)
        };

        return (
            <React.Fragment>

                <ErrorSnackBar
                    open={!!this.state.errorMessage}
                    variant={this.state.errorType}
                    autoHideDuration={5000}
                    onClose={() => this.setState({...this.state, errorMessage: null})}
                    message={this.state.errorMessage?this.state.errorMessage.toString():''}
                />
                <LoadingDialog state={this.state.dialogLoading}/>

                <Dialog
                    open={this.state.dialogDeleteFolder}
                    onClose={() => this.setState({...this.state, dialogDeleteFolder: false})}
                    classes={{
                        paper: classes.paper
                    }}
                >
                    <DialogContent>
                        <DialogContentText>
                            Are you sure?
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => this.setState({...this.state, dialogDeleteFolder: false})}
                                color="primary">
                            cancel
                        </Button>
                        <Button onClick={() => {
                            this.deleteFolder(this.state.currentFolder)
                        }}
                                color="primary"
                                autoFocus
                        >
                            delete
                        </Button>
                    </DialogActions>
                </Dialog>

                <Dialog
                    open={(!!this.state.dialogAddToFolder)||(!!this.state.cutDialog)}
                    onClose={() => this.setState({...this.state,cutDialog:null, dialogAddToFolder: null, newFolderName:''})}
                    classes={{
                        paper: classes.paper
                    }}
                >
                    <DialogContent>
                        <DialogTitle>
                            Choose your folder:
                        </DialogTitle>
                        <div style={{
                            display:'flex',
                            flexDirection:'column',
                            alignItem:'flex-start',
                            maxHeight:'100px'
                        }}>
                            <GridList cols={1}>
                                {this.state.folders && this.state.folders.map((item,index)=>(
                                    <Button
                                        style={{height:'20px !important'}}
                                        onClick={()=>this.state.cutDialog?
                                            this.cutAllToFolder(item.folder.folderName) :
                                            this.saveAllToFolder(item.folder.folderName)}
                                        mini={true}
                                        color={"primary"}>
                                        {/*{item.folderName}*/}
                                        {item.folder.folderName}
                                    </Button>
                                ))}
                            </GridList>
                            {!this.state.folders && <span>no one folders</span>}
                        </div>
                        {/*<DialogTitle>*/}
                            {/*or create new:*/}
                        {/*</DialogTitle>*/}
                        {/*<InputSingleRow*/}
                            {/*boxShadow={'none'}*/}
                            {/*labelText={'Type a new folder name'}*/}
                            {/*defaultValue={this.state.newFolderName}*/}
                            {/*onChange={(e)=>this.setState({...this.state,newFolderName:e.target.value})}*/}
                        {/*/>*/}
                        {/*<Button onClick={() => {*/}
                            {/*this.createFolder(this.state.newFolderName)*/}
                        {/*}}*/}
                                {/*color="primary"*/}
                                {/*autoFocus*/}
                        {/*>*/}
                            {/*create*/}
                        {/*</Button>*/}
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => this.setState({...this.state,cutDialog:null, dialogAddToFolder: null,newFolderName:''})}
                                color="primary">
                            cancel
                        </Button>
                    </DialogActions>
                </Dialog>

                <Dialog
                    open={!!this.state.dialogCreateNewFolder}
                    onClose={() => this.setState({...this.state,cutDialog:false, dialogCreateNewFolder: null, newFolderName:''})}
                    classes={{
                        paper: classes.paper
                    }}
                >
                    <DialogContent>
                        <DialogTitle>
                            new folder name:
                        </DialogTitle>
                        <InputSingleRow
                            boxShadow={'none'}
                            labelText={'Type a new folder name'}
                            defaultValue={this.state.newFolderName}
                            onChange={(e)=>this.setState({...this.state,newFolderName:e.target.value})}
                        />
                        <Button onClick={() => {
                            this.createFolder(this.state.newFolderName)
                        }}
                                color="primary"
                                autoFocus
                        >
                            create
                        </Button>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => this.setState({...this.state, dialogCreateNewFolder: null,newFolderName:''})}
                                color="primary">
                            cancel
                        </Button>
                    </DialogActions>
                </Dialog>

                <Dialog
                    open={!!this.state.dialogEditFolder}
                    onClose={() => this.setState({...this.state, dialogEditFolder: null, newFolderName:''})}
                    classes={{
                        paper: classes.paper
                    }}
                >
                    <DialogContent>
                        <DialogTitle>
                            new name:
                        </DialogTitle>
                        <InputSingleRow
                            boxShadow={'none'}
                            labelText={'Type a new folder name'}
                            defaultValue={this.state.newFolderName}
                            onChange={(e)=>this.setState({...this.state,newFolderName:e.target.value})}
                        />
                        <Button onClick={() => {
                            this.createFolder(this.state.newFolderName,this.state.prevFolderName)
                        }}
                                color="primary"
                                autoFocus
                        >
                            confirm
                        </Button>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => this.setState({...this.state, dialogEditFolder: null,newFolderName:''})}
                                color="primary">
                            cancel
                        </Button>
                    </DialogActions>
                </Dialog>





                <div style={{display: 'flex'}}>

                    <CssBaseline/>

                    <React.Fragment>
                        <Hidden smUp implementation="css">
                            <SwipeableDrawer
                                keepMounted={true}
                                hysteresis={.60}
                                swipeAreaWidth={20}
                                disableEnforceFocus={true}
                                disableAutoFocus={true}
                                disableRestoreFocus={true}
                                disableSwipeToOpen={false}
                                container={this.props.container}
                                variant="temporary"
                                open={this.state.mobileOpenL}
                                onClose={this.handleDrawerToggleL}
                                onOpen={this.handleDrawerToggleL}
                            >
                                {drawerL}

                            </SwipeableDrawer>
                        </Hidden>
                        <Hidden xsDown implementation="css">
                            <Drawer
                                variant="permanent"
                                open
                            >
                                {drawerL}
                            </Drawer>
                        </Hidden>
                    </React.Fragment>


                    <LoadingDialog state={this.state.dialogLoading}/>


                    <main className={classes.content}>

                        <div style={{
                            position: 'absolute',
                            width: '100%',
                            display: 'flex',
                            justifyContent: 'center'
                        }}>

                            {this.state.chosenBar.number > 0 &&
                            <div style={{
                                position: 'fixed',
                                display: 'flex',
                                padding: 5,
                                justifyContent: 'space-between',
                                color: '#01415C',
                                alignItems: 'center',
                                fontWeight: 700,
                                fontSize: '1.1em',
                                zIndex: 1300,
                                minWidth: '300px',
                                marginTop: '.5em',
                                width: '55%',
                                // height: '3em',
                                background: '#e9eefd',
                                borderRadius: 3,
                                boxShadow: '0px 0px 5px 0px rgba(0,0,0,0.25)'
                            }}>

                                <div>
                                    Selected: {this.state.chosenBar.number}
                                    <div>
                                    <Tooltip title="Select all">
                                    <IconButton
                                        size={"small"}
                                        onClick={() => this.selectAll('select')}
                                    >
                                        <i style={{
                                            color: '#01415C',
                                        }} className="fas fa-check-double"/>
                                    </IconButton>
                                    </Tooltip>
                                    {/*<Tooltip title="Delete CV">*/}
                                    {/*<IconButton*/}
                                        {/*onClick={() => {*/}
                                            {/*this.getUserFolders();*/}
                                            {/*this.setState({...this.state, dialogAddToFolder: true})*/}
                                        {/*}}*/}
                                        {/*style={{*/}
                                            {/*color: '#01415C',*/}
                                        {/*}}>*/}
                                        {/*stf*/}
                                    {/*</IconButton>*/}
                                    <Tooltip title="Copy to another folder">
                                    <IconButton
                                        onClick={()=>this.setState({...this.state,dialogAddToFolder:true})}
                                        >
                                        <i style={{
                                            color: '#01415C',
                                        }} className="fas fa-copy"/>
                                    </IconButton>
                                    </Tooltip>
                                    <Tooltip title="Cut to folder">
                                    <IconButton
                                        onClick={()=>this.setState({...this.state,cutDialog:true})}
                                    >
                                        <i style={{
                                            color: '#01415C',
                                        }} className="fas fa-cut"/>
                                    </IconButton>
                                    </Tooltip>
                                    <Tooltip title="Delete CV">
                                    <IconButton
                                        onClick={this.deleteChosenCv}
                                    >
                                        <i style={{
                                            color: '#01415C',
                                        }} className="fas fa-trash-alt"/>
                                    </IconButton>
                                    </Tooltip>
                                    </div>
                                    {/*<Dialog*/}
                                    {/*open={this.state.dialogDelete}*/}
                                    {/*onClose={() => this.setState({...this.state, dialogDelete: false})}*/}
                                    {/*classes={{*/}
                                    {/*paper: classes.paper*/}
                                    {/*}}*/}
                                    {/*>*/}
                                    {/*<DialogContent>*/}
                                    {/*<DialogContentText>*/}
                                    {/*Delete forever?*/}
                                    {/*</DialogContentText>*/}
                                    {/*</DialogContent>*/}
                                    {/*<DialogActions>*/}
                                    {/*<Button onClick={() => this.setState({...this.state, dialogDelete: false})}*/}
                                    {/*color="primary">*/}
                                    {/*no*/}
                                    {/*</Button>*/}
                                    {/*<Button onClick={() => {*/}
                                    {/*this.deleteCv()*/}
                                    {/*}}*/}
                                    {/*color="primary"*/}
                                    {/*autoFocus*/}
                                    {/*>*/}
                                    {/*delete*/}
                                    {/*</Button>*/}
                                    {/*</DialogActions>*/}
                                    {/*</Dialog>*/}
                                </div>
                                <div>
                                    <Tooltip title="Unselect all">
                                    <IconButton
                                        onClick={() => this.selectAll('unselect')}
                                        style={{
                                            color: '#01415C',
                                        }}>
                                        <Close/>
                                    </IconButton>
                                    </Tooltip>
                                </div>
                            </div>}


                        </div>


                        <Fab
                            color="primary"
                            aria-label="Open palette drawer"
                            onClick={this.handleDrawerToggleL}
                            className={classes.menuButtonEditor}
                        >
                            <Icon>menu</Icon>
                        </Fab>


                        <div  ref={this.topPage} style={{
                            width: '100%',
                            display: 'flex',
                            justifyContent: 'flex-start',
                            padding:'.2em .5em',
                            fontWeight:400,
                            fontSize:'1.5em',
                            color:'#01415C'
                        }}>
                            {this.state.isOpen[1] && this.state.currentFolder&&
                            <span><span style={{fontWeight:700}}>{this.state.currentFolder+': '}</span>{this.state.chosenCVs&&this.state.chosenCVs[this.state.currentFolder]?
                                Object.keys(this.state.chosenCVs[this.state.currentFolder]).length:''}</span>}
                        </div>


                        {this.state.isOpen[1] &&
                        this.state.folders &&
                        this.state.folders.map((list,index)=>{
                           if (list.folder.folderName === this.state.currentFolder && this.state.chosenCVs[list.folder.folderName]){
                               return list.folder.cvList.map((item,index)=>(
                                        <div className='fadeIn' style={{padding:'1em'}} key={index}
                                        >
                                            <ExpansionPanel className={classes.expansionPanel} square={true}
                                                // onChange={this.handleOpenPanel(item.cvId)}
                                            >
                                                <ExpansionPanelSummary>
                                                    <div style={{
                                                        display:'flex',
                                                        width:'100%',
                                                        maxWidth:'100vw',
                                                        alignItems:'baseline',
                                                        justifyContent:'center',
                                                        flexWrap:'wrap'
                                                    }}>
                                                        <div style={{alignSelf:'self-start'}}>
                                                            {this.state.chosenCVs &&
                                                            <Checkbox
                                                                icon={<CheckBoxOutlineBlankOutlined fontSize={"small"}/>}
                                                                checkedIcon={<CheckBoxOutlined fontSize={"small"}/>}
                                                                onClick={(e) => e.stopPropagation()}
                                                                onChange={this.handleChecked(item.cvId)}
                                                                checked={this.state.chosenCVs[list.folder.folderName][item.cvId]}
                                                                // value={this.state.chosenCVs[list.folder.folderName][item.cvId]}
                                                            />}
                                                        </div>
                                                        <div style={{minWidth:'150px',lineHeight:'30%',flex: 1, color: '#01415C'}} className={classes.column}>
                                                        <span
                                                            style={{fontSize: '1.5em', fontWeight: '600'}}>{item.title.firstname}</span>
                                                            <p style={{fontSize: '.95rem'}}>{item.description.preferableArea[0]}</p>
                                                            <p style={{fontSize: '.9rem'}}>{item.description.about}</p>
                                                            <div style={{
                                                                display:'flex',
                                                                overflow:'hidden',
                                                            }}>
                                                                {skillsOnPanel(item)}
                                                            </div>
                                                        </div>
                                                        <div style={{
                                                            flex:.4,
                                                            display:'flex',
                                                            minWidth:'150px',
                                                            width:'20%',
                                                            wordBreak:'break-all',
                                                            fontSize:'.9em',
                                                            color:'#01415C',
                                                            margin:'1em 0 1em 0',
                                                            flexDirection:'column',
                                                            // lineHeight:'10%'
                                                        }}>
                                                            {item.contactInfo.phone && <span style={{display:'flex',alignItems:'center'}}><Smartphone fontSize={"inherit"}/>{item.contactInfo.phone}</span>}
                                                            {item.contactInfo.email && <span style={{display:'flex',alignItems:'center'}}><Email fontSize={"inherit"}/>{item.contactInfo.email}</span>}
                                                            {item.contactInfo.residence && <span style={{display:'flex',alignItems:'center'}}><LocationCity fontSize={"inherit"}/>{item.contactInfo.residence}</span>}
                                                            {item.contactInfo.birthday && <span style={{display:'flex',alignItems:'center'}}>
                                                                <Person fontSize={"inherit"}/>{this.getAge(item.contactInfo.birthday)}
                                                            </span>}
                                                        </div>
                                                        {item.isProved &&
                                                        <img
                                                            src={img.group595a2x}
                                                            style={{margin:5,flex:.2,alignSelf: 'center', width: '10%'}}
                                                            alt="logo"/>}
                                                        {!item.isProved && <img src={img.group595a2x} style={{
                                                            opacity: .5,
                                                            alignSelf: 'center',
                                                            width: '4rem'
                                                        }} alt="logo"/>}
                                                        <div></div>
                                                    </div>

                                                </ExpansionPanelSummary>
                                                <ExpansionPanelDetails>
                                                    <div style={{
                                                        minHeight: '30vh',
                                                        width: '100%'
                                                    }}>
                                                        {this.state.refreshButton && !this.state.cvData[item.cvId] &&

                                                        <div
                                                            className='fadeIn'
                                                            style={{
                                                                display:'flex',
                                                                alignItems:'center',
                                                                justifyContent:'center',
                                                                height:'10vh'
                                                            }}
                                                        >
                                                            <IconButton
                                                                onClick={this.handleOpenPanel(item.cvId)}
                                                            >
                                                                {/*<Refresh/>*/}
                                                            </IconButton>
                                                        </div>
                                                        }
                                                        <React.Fragment>
                                                            <TemplateComponent data={item}/>
                                                            {/*<div style={{*/}
                                                            {/*marginTop: 10,*/}
                                                            {/*display: 'flex',*/}
                                                            {/*justifyContent: 'flex-end'*/}
                                                            {/*}}>*/}
                                                            {/*<Button*/}
                                                            {/*variant={"contained"}*/}
                                                            {/*color={"primary"}*/}
                                                            {/*size={"small"}*/}
                                                            {/*// style={{padding: '0 50px 0 50px'}}*/}
                                                            {/*component={Link}*/}
                                                            {/*to={{pathname: '/job_seeker/edit',state:{cv:{...this.state.cvData[item.cvId]}}}}*/}
                                                            {/*>Edit</Button>*/}
                                                            {/*</div>*/}
                                                        </React.Fragment>
                                                    </div>
                                                </ExpansionPanelDetails>
                                            </ExpansionPanel>
                                        </div>
                                    )
                                ) }
                              }
                            )}






                        <div style={{padding: '1em'}}>
                            <ActivateAdvertising/>
                        </div>

                    </main>

                    <React.Fragment>
                        {/*<Hidden smUp implementation="css">*/}
                        {/*<SwipeableDrawer*/}
                        {/*container={this.props.container}*/}
                        {/*variant="temporary"*/}
                        {/*anchor='right'*/}
                        {/*open={this.state.mobileOpenR}*/}
                        {/*onClose={this.handleDrawerToggleR}*/}
                        {/*onOpen={this.handleDrawerToggleR}*/}
                        {/*>*/}
                        {/*{drawerR}*/}
                        {/*</SwipeableDrawer>*/}
                        {/*</Hidden>*/}
                        <Hidden xsDown implementation="css">
                            <Drawer
                                variant="permanent"
                                anchor="right"
                                open
                            >

                            </Drawer>
                        </Hidden>
                    </React.Fragment>

                </div>
            </React.Fragment>
        );
    }
}


export default withStyles(styles)(HiringManageCVsComponent);
