import React, {Component, Fragment} from 'react';

import { connect} from "react-redux";
import * as hiringActions from "./store/actions"
import {Redirect, Route, Switch} from "react-router-dom";
import HiringManageCVsComponent from "./components/HiringManageCVs.component";
import HiringSearchCVsComponent from "./components/HiringSearchCVs.component";

class HiringContainerComponent extends Component {
    render() {
        return (

            <Fragment>

                <Switch>
                <Route exact path='/hiring/manageCVs'
                       render={()=>
                           localStorage.getItem('tooken')&&
                           localStorage.getItem('hrData')?
                               <HiringManageCVsComponent data={this.props}/>:
                               <Redirect to={'/'}/>}/>
                <Route exact path='/hiring/search'
                       render={()=>
                           localStorage.getItem('tooken')&&
                           localStorage.getItem('hrData')?
                               <HiringSearchCVsComponent data={this.props}/>:
                               <Redirect to={'/'}/>}/>
                <Route path="*" render={()=>{
                    localStorage.removeItem('data_cv');
                    return(<Redirect to={'/'}/>)
                }}/>
            </Switch>
                {/*<Route path='/job_seeker/edit' component={EditorComponent}/>*/}
            </Fragment>

        );
    }
}
function mapStateToProps(state) {
    return {
        ...state
    }
}

export default connect(mapStateToProps, hiringActions)(HiringContainerComponent);
