export function DrawerListItem ({
    style,
    isOpen = false,
    onClick = (event)=>console.log('set onClick, event:'+ event),
    titleText = 'title text',
    children
    }) {
    return (
        <React.Fragment>
            <ListItem
                style={{...style}}
                disableRipple={true}
                button
                onClick={onClick}
                disableGutters={true}
            >
                <ListItemText  disableTypography={true}>
                    {titleText}
                </ListItemText>
                {isOpen
                    ? <i className="far fa-dot-circle" style={{
                        color: 'rgba(255, 255, 255, 0.8)',
                        fontSize:'.6rem'
                    }}/>
                    : <i className="far fa-circle"   style={{
                        color: 'rgba(255, 255, 255, 0.6)',
                        fontSize:'.6rem'
                    }}/>}
            </ListItem>
            <Collapse  in={isOpen} timeout="auto" >
                <List component="div" disablePadding>
                    {children}
                </List>
            </Collapse>
        </React.Fragment>
    );
}

