export function InputValidateSingleRow ({
    id,
    labelText = 'label',
    defaultValue = '',
    value,
    maxInputLength = 20,
    onChange = (event)=>console.log('set onChange, value is:'+event.currentTarget.value),
    required = false,
    border = false,
    boxShadow,
    type = 'text',
    password = false,
    showPassword = true,
    adornmentOnClick,
    validators,
    errorMessages,
    handleSubmit,
    helperText
}) {
    const passwordAdornment=(
    <InputAdornment position="end">
        <IconButton
            aria-label="Toggle password visibility"
            onClick={adornmentOnClick}
        >
            {showPassword ? <Visibility /> : <VisibilityOff />}
        </IconButton>
    </InputAdornment>
    );
    return (

        //need validate form parent for using this

        <TextValidator
            helperText={helperText}
            errorMessages={errorMessages}
            validators={validators}
            id={id}
            style={{
                border: border?'1px solid grey':'',
                boxShadow: boxShadow,
            }}
            defaultValue={defaultValue}
            value={value}
            label={labelText}
            required={required}
            margin="dense"
            fullWidth
            onChange={onChange}
            FormHelperTextProps={{
            }}
            InputProps={{
                type: !showPassword && password ?'password': type,
                endAdornment: password && passwordAdornment,
                inputProps: {
                    maxLength: maxInputLength,
                }, disableUnderline: true
            }}
        />
    );
}


