export function SelectInput ({
    id,
    native = false,
    selectProps,
    inputRef,
    children,
    labelText = 'label',
    disabled = false,
        value = '',
        onChange = (event) => console.log('set onChange, value is:' + event.target.value),
        required = false,
        options
}
) {
    return (
        <TextField
            id={id}
            inputRef={inputRef}
            disabled={disabled}
            label={labelText}
            margin="dense"
            fullWidth
            select={true}
            required={required}
            value={value}
            SelectProps={{
                ...selectProps,
                native:native,
                // endAdornment: <InputAdornment position="start">$</InputAdornment>,
                disableUnderline: true,
            }}
            onChange={onChange}
        >
            {!children && options && options.map((option, index) => (
                <MenuItem key={index} value={option.value?option.value:option}>
                    {option.label ? option.label : ''} {option.value?option.value:option}
                </MenuItem>
            ))}

            {children}
        </TextField>
    );
}

export function InputChips ({
    labelText = 'label',
    defaultValue = ['angular','react'],
    allowDuplicates = true,
    maxLength = 20,
    onChange = (event)=>console.log(event),
    placeholder = 'Type and press enter to add chips'
    }) {

    const data =[
        'Java', 'Android', 'JavaScript', 'HTML', 'CSS', 'Angular',
        'OOP', 'AOP', 'ORM Hibernate', 'JDBC', 'Spring',
        'Multithreading', 'Apache Tomcat', 'JSON', 'Maven', 'JUnit', 'REST', 'GitLab',
        'Eclipse', 'Intellij IDEA', 'Android Studio', 'WebStorm',
        'MySQL', 'SQLite', 'MongoDB',
        'Linux', 'Windows', 'OS X'
    ];

    return(
        <ChipInput
            dataSource={data}
            style={{borderRadius: 10, padding: '0 5px 10px 5px'}}
            defaultValue={defaultValue}
            onChange={onChange}
            label={labelText}
            placeholder={placeholder}
            disableUnderline={true}
            fullWidth
            fullWidthInput
            allowDuplicates={allowDuplicates}
            margin={"dense"}
            variant={"standard"}
            InputProps={{inputProps:{maxLength: maxLength}}}
        />
    );
}

