export function RangeInput ({
    style,
    big = false,
    titleText = 'Chose some',
    titleText2,
    value =  {
        min: 3000,
        max: 6000,
        },
    minValue = 1000,
    maxValue = 10000,
    step = 500,
    label = '$',
    onChange = res => console.log(res),
    onChangeComplete = (value) => console.log(value),
}) {

    return (
        <form  className={!big?'input-range-container':'input-range-container-big'}>
            <div onClick={(e)=>e.stopPropagation()} onFocus={(e)=>e.stopPropagation()} style={{
                ...style,
                display:'flex',
                justifyContent: 'space-between'
            }}>
                <span className='input-range__title'>{titleText}</span>
                {titleText2 && <span className='input-range__title'>{titleText2}</span>}
            </div>
            <br/>
            <InputRange
                draggableTrack
                step={step}
                onChange={onChange}
                minValue={minValue}
                maxValue={maxValue}
                formatLabel={value => `${value} ${label}`}
                value={value}
                onChangeComplete={onChangeComplete}
            />
        </form>
    );
}



