import React, {Component, Fragment} from 'react';
import './job_seeker.container.component.scss';

import { connect} from "react-redux";
import EditorComponent from "./components/editor/editor.component";
import * as jobSeekerActions from "./store/actions"
import {Redirect, Route, Switch} from "react-router-dom";
import ManageCVsComponent from "./components/ManageCVs.component";
import SettingsComponent from "./components/Settings.component";
import StatisticComponent from "./components/Statistic.component";

class JobSeekerContainer extends Component {
    render() {
        return (

            <Fragment>
                <Switch>
                <Route exact path='/job_seeker/manageCVs'
                       render={()=>{
                           if (localStorage.getItem('tooken') &&
                               localStorage.getItem('jsData')) {
                               return (<ManageCVsComponent data={this.props}/>)
                           }else {
                               localStorage.removeItem('data_cv');
                               return(<Redirect to={'/'}/>)
                           }
                       }}/>
                <Route exact path='/job_seeker/settings'
                       render={()=>{
                           if (localStorage.getItem('tooken') &&
                               localStorage.getItem('jsData')) {
                               return (<SettingsComponent data={this.props}/>)
                           }else {
                               localStorage.removeItem('data_cv');
                               return(<Redirect to={'/'}/>)
                           }
                       }}/>
                <Route exact path='/job_seeker/statistic'
                       render={()=>{
                           if (localStorage.getItem('tooken') &&
                               localStorage.getItem('jsData')) {
                               return (<StatisticComponent data={this.props}/>)
                           }else {
                               localStorage.removeItem('data_cv');
                               return(<Redirect to={'/'}/>)
                           }
                       }}/>
                <Route path='/job_seeker/edit' component={EditorComponent}/>
                <Route path="*" render={()=>{
                    localStorage.removeItem('data_cv');
                    return(<Redirect to={'/'}/>)
                }}/>
            </Switch>
            </Fragment>

        );
    }
}
function mapStateToProps(state) {
    return {
        ...state
    }
}

export default connect(mapStateToProps, jobSeekerActions)(JobSeekerContainer);
