    handleAddChip = (chip) => {
      this.setState( state => {
             return {
                 ...state,
                 chipData: [
                     ...state.chipData,
                        chip
                    ]
                }
          }
      )
    };

    handleDeleteChip = (chip, index) => {
        this.setState(state => {
            const arr = state.chipData;
            arr.splice(index,1);
            return { ...state,
                chipData: [
                    ...arr
                ]
            };
        });
        //
        // console.log(this.state.chipData)
    };

    menuHandleClick = event => {
        this.setState(
            {
                ...this.state,
                isOpen: {
                    ...this.state.isOpen, menu: event.currentTarget
                } });
    };

    menuHandleClose = () => {
        this.setState(
            {
                ...this.state,
                isOpen: {
                    ...this.state.isOpen, menu: null
                } });
    };

    handleDrawerToggleL = () => {
        this.setState(state => ({
            ...this.state,
            mobileOpenL: !state.mobileOpenL }));
    };

    handleDrawerToggleR = () => {
        this.setState(state => ({
            ...this.state,
            mobileOpenR: !state.mobileOpenR }));
    };

    toggleOpen = (number) => {
        this.setState(state => ({ ...state, isOpen: {
            ...this.state.isOpen,
                [number]: !this.state.isOpen[number]
            } }));
    };


